.copyright {
  margin: 0;
}

.copyright__link {
  display: flex;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 112%;
  color: #444444;
  background-color: #f2f2f2;
  text-decoration: none;
  justify-content: space-between;
  align-items: center;

  @media (min-width: $desktop-width) {
    justify-content: flex-end;
  }
}
